.about-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0a0a, #1a002b);
  color: white;
  text-align: center;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* GIRL image (smaller + better gap) */
.about-girl {
  width: 200px;
  margin-top: 10px;
  margin-bottom: -50px;
  /*animation: float 3s ease-in-out infinite;*/
}

/* TITLE */
.about-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 15px;
}

/* TEXT */
.about-text {
  font-size: 1.2rem;
  width: 65%;
  line-height: 1.6;
  margin-bottom: 30px;
}

/* BUNNY (smaller & close to button) */
.about-bunny {
  width: 130px;
  margin-top: -20px;
  margin-bottom: -105.08px;
  /*animation: float 3s ease-in-out infinite;*/
}

/* BUTTON */
.portfolio-btn {
  margin-top: 20px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  padding: 12px 28px;
  border-radius: 10px;
  color: white;
  font-size: 1.1rem;
  cursor: pointer;
  transition: 0.3s;
}

.portfolio-btn:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.35);
}

/* FLOAT animation (same as welcome page)
@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
  100% { transform: translateY(0); } 
} */

/* MOBILE */
@media (max-width: 768px) {
  .about-text {
    width: 90%;
    font-size: 1rem;
  }

  .about-girl {
    width: 180px;
  }

  .about-bunny {
    width: 130px;
  }

  .about-title {
    font-size: 2rem;
  }
}