/* ---- MAIN PAGE ---- */
.welcome-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #0a0a0a, #1a002b);
  text-align: center;
}

/* ---- BUNNY IMAGE ---- */
.bunny-img {
  width: 300px;                 /* correct size */
  margin-top: -40px;            /* reduce top gap */
  margin-bottom: -28px;          /* small space before text */
  animation: float 4s ease-in-out infinite,
             wave 2s ease-in-out infinite;
}

/* FLOAT ANIMATION */
@keyframes float {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-8px); }
  100% { transform: translateY(0); }
}

/* WAVE ANIMATION */
@keyframes wave {
  0%   { transform: rotate(0deg); }
  20%  { transform: rotate(5deg); }
  40%  { transform: rotate(-5deg); }
  60%  { transform: rotate(5deg); }
  80%  { transform: rotate(-3deg); }
  100% { transform: rotate(0deg); }
}

/* ---- TEXT ---- */
.welcome-text {
  font-size: 1.8rem;
  font-weight: bold;
  color: white;
  margin-bottom: 25px;          /* INCREASE gap before buttons */
}

/* ---- BUTTON ROW ---- */
.button-row {
  display: flex;
  gap: 80px;                    /* better spacing */
}

/* ---- BUTTON STYLE ---- */
.btn {
  padding: 10px 26px;
  border-radius: 10px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

/* YES BUTTON */
.yes {
  background: #b47fff;
  color: white;
}

/* NO BUTTON */
.no {
  background: #2e0147;
  color: #e5d4ff;
  border: 1px solid #b47fff;
}

/* ---- HOVER EFFECT ---- */
.btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 18px rgba(255, 255, 255, 0.2);
}